import React,{useState} from 'react'
import { makeStyles,Button } from '@material-ui/core';
import Popover from '@material-ui/core/Popover';
import Typography from '@material-ui/core/Typography';

export default function Time_popover({timer,setTimer}) {
  const [newTime,setNewTime]=useState(timer)
    const [anchorEl, setAnchorEl] = React.useState(null);
    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
      };
    
      const handleClose = () => {
        setAnchorEl(null);
        setTimer(newTime)
      };

      const open = Boolean(anchorEl);
  const id = open ? 'simple-popover' : undefined;
    return (
        <div>
        <Button onClick={ handleClick }  variant='outlined' size="small">עדכן זמן</Button>
             <Popover
        id={id}
        open={open}
        anchorEl={anchorEl}
      
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'center',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'center',
        }}
      >
       <input onChange={(e)=>{setNewTime(e.target.value)}} type='number' value={newTime}/>
       <button onClick={handleClose}>עדכן</button>
      </Popover> 
        </div>
    )
}
