import React,{useState} from 'react'
import { makeStyles,Grid } from '@material-ui/core';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import '../../../css/orders.css'
import Order_ready from './Order_ready';
import Time_popover from './Time_popover';

const useStyles = makeStyles({
    root: {
        minWidth: 275,
      textAlign:'right',
      boxShadow: "rgba(0, 0, 0, 0.35) 0px 5px 15px",
      borderRadius:'25px',
      // background:"rgb(245, 237, 229)"
    },
    bullet: {
      display: 'inline-block',
      margin: '0 2px',
      transform: 'scale(0.8)',
    },
    title: {
      fontSize: 14,
    },
    pos: {
      marginBottom: 12,
    },
  });

  
export default function NewOrder({el,timer,setTimer,token,setPreview_orders,setNewOrder,setLoader,newOrder,preview_orders}) {


    const classes = useStyles();
  const bull = <span className={classes.bullet}>•</span>;
    return (
        <Card  className={classes.root}>
      <CardContent>
          {/* <h4>  זמן הכנה: נותר <span style={{color:"green",fontSize:'18px'}}>{timer}</span>  דקות </h4> */}
          <h4>בימים הקרובים יהיה כאן זמן הכנה.</h4>
          <Divider  style={{marginBottom:'5px',marginTop:'10px'}} variant="right" />
        <Typography variant="h6" component="h6">
        {new Date(el.date).toLocaleString()}
      

        </Typography>
        <Divider  style={{marginBottom:'5px',marginTop:'10px'}} variant="right" />
        <Typography  component="h5" color="textSecondary">
         זמן הגעה: {el.arrival_time}
         
        </Typography>
       
        <Typography className={classes.pos} color="textSecondary">
        : הערות  {el.comment}
        </Typography>
        <Typography variant="body2" component="p">
        <Divider  style={{marginBottom:'5px',marginTop:'10px'}} variant="right" />
              {el.items.map(item=>{
                  return(
                      <Grid>
                          <h3>{item.amount} . {item.name}   </h3>
                      </Grid>
                  )
              })
          }
        </Typography>
        <Divider   variant="right" />
      </CardContent>
      <div style={{display:'flex'}}>
      <CardActions>
      <Time_popover
      timer={timer}
      setTimer={setTimer}
      />
      </CardActions>
      <CardActions>
      <Order_ready 
      el={el}
      token={token}
      setPreview_orders={setPreview_orders}
      setNewOrder={setNewOrder}
      setLoader={setLoader}
      newOrder={newOrder}
      preview_orders={preview_orders}
      />
      </CardActions>
      </div>
      
    </Card>
    )
}
